<template>
  <div>
    <waterfall 
      :col="col" 
      :data="data" 
      @loadmore="loadmore"
      @scroll="scroll"
    >
      <template>
        <div class="cell-item" v-for="(item, index) in data" :key="index">
          <img @click="openDialog(item.title,item.articledesc,item.content,item.pic,item.likes)" :lazy-src="'http://8.130.20.53/image' + item.pic[0]" :key="item.pic[0]" alt="加载错误" />
          <div class="item-body">
            <div class="item-desc">{{ item.articledesc }}</div>
            <div class="item-footer">
              <div class="footer-left">
                <img src="@/assets/avatar/1.png" alt="" srcset="" />
                <div class="name">Stephen</div>
              </div>
              <div class="like">
                <img style="color: #aaa;" @click="changeLike(item,index)" ref="Like"  class="fa fa-heart" src="" alt="" />
                <!-- <i class="fa fa-heart" /> -->
                <div class="like-total">{{ item.likes }}</div>
              </div>
            </div>
          </div>
          <el-dialog :visible.sync="dialogTableVisible" :modal-append-to-body='false'>
            <MainInfo :list="currentArticle"></MainInfo>
          </el-dialog>
        </div>

      </template>
    </waterfall>
  </div>
</template>
<script>
import MainInfo from '@/views/MainInfo'
export default {
  components:{MainInfo},
  props: {
    data: {
      type: Array,
      default: () => [],
    },
  },
  computed:{
    currentArticle(){
      return this.currentArticle
    }
  },
  data() {
    return {
      col: 5,
      dialogTableVisible: false,
      currentArticle:[],
      Like: false,
    };
  },
  computed:{
    itemWidth(){  
      return (138*0.5*(document.documentElement.clientWidth/375)) // #rem布局 计算宽度
    },
    gutterWidth(){
      return (9*0.5*(document.documentElement.clientWidth/375))	//#rem布局 计算x轴方向margin(y轴方向的margin自定义在css中即可)
    },
  },
  methods: {
    openDialog(title,articledesc,content,pic,likes){
      this.dialogTableVisible = true
      this.currentArticle = {
        title: title,
        articledesc: articledesc,
        content: content,
        pic: pic,
        likes: likes
      }
      this.$store.state.currentArticle = this.currentArticle
      console.log(this.currentArticle);
    },
    changeLike(item,index){
      this.Like = !this.Like
      if(this.Like == false){
        this.$refs.Like[index].style.color = '#aaa'
      }else{
        this.$refs.Like[index].style.color = 'red'
        item.like ++
      }
    },
    scroll(scrollData) {
      // console.log(scrollData)
    },
    loadmore(index) {
      console.log('加载更多');
    }
  },
};
</script>
<style lang="scss" scoped>
.vue-waterfall{
  padding: 10px;
}
::v-deep .vue-waterfall .vue-waterfall-column{
    width: 19% !important;
}
.item-body {
  margin: 9px;
  .item-desc {
    text-align: left;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #000000;
  }
  .item-footer {
    display: flex;
    justify-content: space-between;
    .footer-left {
      display: flex;
      align-items: center;
      font-family: SF Pro Display;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      color: rgba(0, 0, 0, 0.6);
      img {
        border-radius: 50%;
        width: 22px;
        height: 22px;
        margin-right: 4px;
      }
    }
    .like {
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: SF Pro Display;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      // color: rgba(0, 0, 0, 0.4);
      img{
          margin-right: 4px;
      }
    }
  }
}
.cell-item {
  width: 98%;
  margin-bottom: 20px;
  background: #ffffff;
  border-radius: 25px;
  overflow: hidden;
  box-sizing: border-box;
  transition: all 0.2s ease;
  box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.2);
  img {
    width: 100%;
    height: auto;
    display: block;
    cursor: pointer;
  }
}
::v-deep .cell-item:hover{
  scale: 101%;
  background: #cfcfcf;
  box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.15);
}
::v-deep .el-dialog__body{
  padding: 0;
}
::v-deep .el-dialog{
  border-radius: 15px;
  min-width: 745px;
}
::v-deep .el-dialog__wrapper{
  background-color: #ffffffeb;
  overflow: hidden;
}
::v-deep .v-modal{
  display: none;
}
::v-deep .el-dialog__header {
  padding: 0;
}

</style>
