<template>
  <div class="container">
    <div class="nav-container" ref="navContainer"></div>
    <div class="waterfall-container">
      <div class="waterfall">
        <my-waterfall :data="getList"></my-waterfall>
      </div>
      <div class="waterfallInfo"></div>
    </div>
   </div>
</template>
<script>
import myWaterfall from "@/views/MyWaterFall";
export default {
  components: { myWaterfall },
  data() {
    return {
      list:[],
      input:'',
    };
  },
  created(){
    window.addEventListener('scroll',this.waterFallhandleScroll);
    
  },
  mounted(){
    this.init()
    this.input = this.$route.query.input || ''
  },
  computed:{
    getList(){
      return this.$store.state.list
    },
  },
  methods:{
    init(){
      this.$axios.post('/api/SearchArticlesInfo',{input:this.input,list:this.list}).then(res => {
        this.$store.state.list = res.data.data
      })
    }, 
    waterFallhandleScroll(){
      // location.reload();
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // console.log(scrollTop,'滚动距离');
      if(scrollTop > 0){
        // console.log(this.$refs.navContainer);
        // this.$refs.navContainer.style.opacity = 0
      }else if(scrollTop == 0){
        // this.$refs.navContainer.style.opacity = 1
      }
    },
    waterfallbeforeDestroy(){
    	window.removeEventListener('scroll', this.waterFallhandleScroll)
    }
  }
};
</script>

<style scoped>
.container {
  background: url('@/assets/background13.gif');
  width: 100%;
  min-height: 100vh;
}
.nav-container{
  width: 100%;
  height: 90px;
  background-image: url(@/assets/navbg1.png);
  background-size: 100% 100%;
}
.waterfall{
  width: 80%;
  margin: 0 auto;
}
.waterfallInfo{
  width: 25%;
}
</style>